<template>
    <el-container>
        <el-header style="font-size: 40px; background-color: rgb(238, 241, 246)">Quiz后台管理</el-header>
        <el-container>
            <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
                <el-menu :default-openeds="['1']" :default-active="$route.path" router>
                <el-submenu index="1">
                    <template slot="title">管理选项</template>
                    <el-menu-item-group>
                    <el-menu-item index="/user">
                        <span>用户管理</span>
                    </el-menu-item>
                    <el-menu-item index="/question">
                        <span>题目管理</span>
                    </el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                </el-menu>
            </el-aside>
            
            <el-main>
                <!-- 顶部的查询表单 -->
                 <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="用户名">
                        <el-input v-model="formInline.user" placeholder="请输入用户名"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">查询</el-button>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="success" @click="onAddNewUser">添加用户</el-button>
                    </el-form-item>
                </el-form>

                <!-- 显示的table -->
                <el-table :data="tableData" style="width: 80%">
                    <el-table-column label="日期" width="180">
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 10px">{{ scope.row.date }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="姓名"
                    width="180">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="top">
                        <p>姓名: {{ scope.row.name }}</p>
                        <p>住址: {{ scope.row.address }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.name }}</el-tag>
                        </div>
                        </el-popover>
                    </template>
                    </el-table-column>
                    <!-- 新增的密码列 - 直接显示明文 -->
                    <el-table-column
                    label="密码"
                    width="180">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="top">
                        <p>密码: {{ scope.row.password }}</p>
                        <div slot="reference" class="name-wrapper">
                            <span style="margin-left: 10px">{{ scope.row.password }}</span>
                        </div>
                        </el-popover>
                    </template>
                    </el-table-column>
                    <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                    </el-table-column>
                </el-table>

                <br />
                <!-- 分页 -->
                <el-pagination
                    background
                    layout="prev, pager, next"
                    :total="1000">
                </el-pagination>

                <!-- 弹出的添加用户的对话框 -->
                <el-dialog title="添加新用户" :visible.sync="dialogFormVisible">
                    <el-form :model="form">
                        <el-form-item label="用户名" :label-width="formLabelWidth">
                            <el-input v-model="form.name" autocomplete="off"></el-input>
                        </el-form-item>

                        <el-form-item label="密码" :label-width="formLabelWidth">
                            <el-input v-model="form.password" type="password" autocomplete="off"></el-input>
                        </el-form-item>

                        <el-form-item label="确认密码" :label-width="formLabelWidth">
                            <el-input v-model="form.confirmpassword" type="password" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false">取 消</el-button>
                        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                    </div>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
export default {
    data() {
      return {
        dialogFormVisible:false,
        formLabelWidth: '120px',
        form: {
          name: '',
          password: '',
          confirmpassword: ''
        },
        
        formInline: {
          user: '',
          region: ''
        },
        tableData: [{
          date: '2016-05-02',
          name: '王小虎',
          password: '123456',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '张三',
          password: 'abcdef',
          address: '上海市普陀区金沙江路 1517 弄'
        }, {
          date: '2016-05-01',
          name: '李四',
          password: 'qwerty',
          address: '上海市普陀区金沙江路 1519 弄'
        }, {
          date: '2016-05-03',
          name: '王五',
          password: 'admin123',
          address: '上海市普陀区金沙江路 1516 弄'
        }]
      }
    },
    methods: {
        onAddNewUser(){
            this.dialogFormVisible=true;
        },    
        onSubmit() {
            console.log('submit!');
        },
        handleEdit(index, row) {
            console.log(index, row);
        },
        handleDelete(index, row) {
            console.log(index, row);
        }
    }
}
</script>

<style>
.el-menu-item a {
  text-decoration: none;
  color: inherit;
  display: block;
  width: 100%;
}
</style>